import Vue from 'vue'
import Router from 'vue-router'
import ContentT from "../components/index";
import Info from "../components/info.vue";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/info',
      name: 'info',
      component: Info
    },
    {
      path: '/edit',
      name: 'edit',
      component: ContentT
    }
  ]
})



